import React from 'react'
import { Card, Table } from 'antd';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import NumberFormat from 'react-number-format';

const { Title } = Typography;

const PaymentList = ({systemData}) => {

	const { transactions } = systemData;
	
	const tableColumns = [
		{
			title: 'Client',
			dataIndex: 'client',
			render: (_, record) => (
				<div className="d-flex" style={{cursor:'pointer'}}>
					<AvatarStatus size={1} name={record.backupClientName} subTitle={'0'+record.backupClientPhone}/>
				</div>
			),
		},
		{
			title: 'Paid Number',
			dataIndex: 'phone',
			render: (_, record) => record.amount
		},
		{
			title: 'Amount',
			dataIndex: 'amount',
			render: (_, record) => (<NumberFormat displayType={'text'} value={parseInt(record.amount)} thousandSeparator={true}/>)
		},
		{
			title: 'Date',
			dataIndex: 'date',
			render: (_, record) => new Date(record.date).toLocaleDateString()
		}
	];
	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Clients Transactions from MTN</Title>
					</div>
				</Flex>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={ transactions } 
					rowKey='_id' 
				/>
			</div>
		</Card>
	)
}
export default PaymentList