import React, { useState } from 'react'
import { Card, Table, message, Menu, Avatar, Modal, Tag } from 'antd';
import { CheckCircleOutlined, CloseOutlined } from '@ant-design/icons';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import Flex from 'components/shared-components/Flex'
import { DataService } from "services/payments.services";
import { DataService as PaidService } from "services/paid.services";
import { Typography } from "antd";
import NumberFormat from 'react-number-format';
import utils from 'utils';

const { Title } = Typography;
const { confirm } = Modal;
const getUserStatus = status => {
	if(status === 1) {
		return <Tag className ="text-capitalize" color='geekblue'>Pending</Tag>
	}
	else if(status === 2) {
		return <Tag className ="text-capitalize" color='cyan'>Approved</Tag>
	}
	else{
		return <Tag className ="text-capitalize" color='red'>Rejected</Tag>
	}
}
const PaymentList = ({systemData}) => {
	const [userProfileVisible, setUserProfileVisible] = useState(false)
	const [selectedUser, setSelectedUser] = useState(null)
	const { site, sitsLoading, paid, paidLoading, payments, paymentsLoading, avatarColors } = systemData;
	// if(!paidLoading){
	// 	paid.docs.map((record)=>{
	// 		return PaidService.remove(record.id)
	// 	})
	// 	console.log(paid.docs.length)
	// }
	const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => showConfirm(row)}>
				<Flex alignItems="center">
					<CheckCircleOutlined />
					<span className="ml-2">Approve</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => confirmReject(row)}>
				<Flex alignItems="center">
					<CloseOutlined />
					<span className="ml-2">Reject</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);
	
	const approveRow = row => {
		DataService.update(row._id,{...row, status:2});
		PaidService.create({...row, status:2},row._id);
		message.success({ content: `Receipt approved `, duration: 3 });
	}

	const deleteRow = row => {
		DataService.remove(row._id)
		message.error({ content: `Receipt Rejected `, duration: 3 });
	}

	function showConfirm(row) {
		confirm({
		  title: 'Do you want to approve this receipt?',
		  onOk() {
			   approveRow(row)
		  },
		  onCancel() {},
		});
	  }

	function confirmReject(row) {
		confirm({
			title: 'Do you want to reject this receipt?',
			onOk() {
				deleteRow(row)
			},
			onCancel() {},
		});
	}

	const showUserProfile = userInfo => {
		setUserProfileVisible(true)
		setSelectedUser(userInfo)
	};
	
	const closeUserProfile = () => {
		setUserProfileVisible(false)
		setSelectedUser(null)
	}
	const tableColumns = [
		{
			title: 'Receipt',
			dataIndex: 'receipt',
			render: (_, record) => (
				<div className="d-flex" style={{cursor:'pointer'}} onClick={() => {showUserProfile(record.data())}}>
					<AvatarStatus size={60} 
						type="square" 
						src={record.data().image} 
						name={<NumberFormat displayType={'text'} value={parseInt(record.data().amount)} thousandSeparator={true} suffix=" Rwf"/>} 
						subTitle={new Date(record.data().datePaid).toLocaleDateString()}/>
				</div>
			),
		},
		{
			title: () => <div className="pl-2">Client</div>,
			dataIndex: 'client',
			render: (_, record) => (
				<div className="d-flex" style={{cursor:'pointer'}}>
					<AvatarStatus size={1} name={record.data().backupClientName} subTitle={record.data().client}/>
				</div>
			),
		},
		{
			title: () => <div className="pl-2">Uploaded By</div>,
			dataIndex: 'upload',
			render: (_,record) => (
				<div className="d-flex" style={{cursor:'pointer'}}>
					<AvatarStatus size={1} type="square" src={(
						<div className="d-flex align-items-center">
						<Avatar size={1} className="font-size-sm" style={{backgroundColor: avatarColors[Math.floor(Math.random() * avatarColors.length) - 1]}}>
						  {utils.getNameInitial(record.data().name)}
						</Avatar>
					  </div>
					)} name={record.data().name} subTitle={'0'+record.data().phone}/>
				</div>
			  )
		},
		{
			title: 'Status',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{getUserStatus(record.data().status)}</Flex>
			),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) =>(
				<div className="text-right">
					{elm.data().status === 1 && <EllipsisDropdown menu={dropdownMenu(elm.data())}/>}
				</div>
			)
		}
	];
	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Organisation Receipts</Title>
					</div>
				</Flex>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={!paymentsLoading ? payments.docs : []} 
					loading={paymentsLoading}
					rowKey='_id' 
				/>
			</div>
			<Modal
				title={selectedUser?.backupClientName + ' ' + selectedUser?.amount + ' Rwf  on:' + new Date(selectedUser?.date).toLocaleDateString()}
				visible={userProfileVisible}
				onCancel={closeUserProfile}
				width={850}
				footer={null}
				className="p-0"
				>
				{selectedUser && <Avatar size={800} shape="square" src={selectedUser?.image}/>}
			</Modal>
		</Card>
	)
}
export default PaymentList