import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom';
import PaymentList from './payment-list'
import Transactions from './transactions'

const Payments = ({match, systemData}) => {
	return (
		<Switch>
			<Redirect exact from={`${match}`} to={`${match}/list`} />
			<Route path={`${match}/listOrganization`} render={ () => (<PaymentList systemData={systemData}/>) } />
			<Route path={`${match}/list`} render={ () => (<Transactions systemData={systemData}/>) } />
		</Switch>
	)
}

export default Payments

